<template>
    <form class="input-fields" @submit.prevent="submit">
        <div class="title">
            <p style="font-size: 0.7em;font-family: PT Serif,serif;">A verificatiion code will be sent to &#10; the email address provided.</p>
        </div>
        <div class="input-fields">
            <div class="textbox-component custom-form-group">
                <label for="email">
                    Email
                </label>
                <input type="email" :value="emailAddress" required id="email" @change="commit('set_email_address', $event.target.value)" placeholder="jane@doe.com" />
            </div>
        </div>
        <div class="alternative">
            Don't have an account?
            <router-link to="/signup">Sign up here</router-link>
        </div>
        <button type="submit" class="call-to-action btn-custom-astronaut-blue">
            Proceed
        </button>
    </form>
</template>

<script>
import { mapState } from 'vuex';
export default {
    computed: mapState(['emailAddress']),
    methods: {
        submit() {
            this.$root.request("POST", "/auth", { emailAddress: this.emailAddress }, function(err, result) {
                if (err) console.log("error from post: ", window.err = err, err.response);
                console.log("success...", result);
                this.$router.push('/login/verify');
            }.bind(this));
        },
        commit(mutationName, payload) {
            this.$store.commit(mutationName, payload);
        }
    },
}
</script>
