<template>
    <form @submit.prevent="verify">
        <div class="account__form">
            <img src="src/img/account.svg" alt="Account" class="account__form-icon">
            <div class="title">
                <span>{{$route.query.message}}</span>
                <p style="font-size: 0.7em;font-family: PT Serif,serif;">Enter the code sent to {{emailAddress}}</p>
            </div>
            <div class="input-fields">
                <div class="textbox-component custom-form-group">
                    <label for="code">
                        Verification Code
                    </label>
                    <input type="text" id="code" @change="commit('set_verification_code', $event.target.value)" required />
                </div>
            </div>
            <div class="alternative">
                Don't have an account?
                <router-link to="/signup">Sign up here</router-link>
            </div>
            <button type="submit" class="call-to-action btn-custom-astronaut-blue">
                Verify
            </button>
        </div>
    </form>
</template>

<script>
import { mapState } from 'vuex';
export default {
    computed: mapState(['emailAddress', 'verificationCode']),
    methods: {
        verify() {
            this.$root.request("GET", `/auth/?code=${this.verificationCode}`, function(err, result) {
                if (err) return console.log("error from post: ", window.err = err, err.response);
                console.log("success...", result)
                localStorage.COINALLY_AUTH_TOKEN = result.token;
                let nextPage = this.$route.query.nextPage;
                if (nextPage) {
                    this.$router.push(nextPage)
                }
            }.bind(this))
        },
        commit(mutationName, payload) {
            this.$store.commit(mutationName, payload);
        }
    },
}
</script>
