<template>
    <div>
        <div class="modal-header clearfix">
            <span class="modal-title">
                Verified
            </span>
            <button type="button" data-dismiss="modal"></button>
        </div>
        <div class="modal-body">
            <div class="modal-body__container">
                <div class="modal__pane" id="assets-selection">
                    <div class="modal__title">
                        Choose which assets to trade
                    </div>

                    <div class="input-fields">
                        <div class="select-component custom-form-group">
                            <label for="deposit">Deposit</label>
                            <vue-select :options="assets" @input="dispatch('set_deposit_asset', $event)" label="name">
                                <template slot="option" slot-scope="option">
                                    <img :src="`http://localhost:9000/${option.imagePath}`" style="height:2rem; margin-right: .3rem; margin-bottom: 0; line-height: 1"> {{option.name}}
                                </template>
                            </vue-select>
                        </div>
                        <div class="select-component custom-form-group">
                            <label for="receive">Receive</label>
                            <vue-select :options="assets" @input="dispatch('set_receipt_asset', $event)" label="name">
                                <template slot="option" slot-scope="option">
                                    <img :src="`http://localhost:9000/${option.imagePath}`" style="height:2rem; margin-right: .3rem; margin-bottom: 0; line-height: 1"> {{option.name}}
                                </template>
                            </vue-select>
                        </div>
                        <div class="textbox-component custom-form-group">
                            <label for="amount">
                                Amount (Min: 0, Max: 100)
                            </label>
                            <input @input="commit('set_amount', $event.target.value)" type="number" :value="amount" id="amount" placeholder="0" />
                        </div>
                    </div>

                    <!-- REMOVE THE toggle-pane CLASS IN PRODUCTION -->

                    <router-link to="/transaction/account" class="call-to-action btn-custom-astronaut-blue">Convert</router-link>

                </div>
            </div>

        </div>
    </div>
</template>
<script>
import { mapState, mapGetters } from 'vuex';
import vueSelect from 'vue-select';
export default {
    computed: Object.assign({}, mapState([
        'depositAsset',
        'receiptAsset',
        'assets',
        'amount',
        'request',
        'conversionRate']),
        mapGetters(['receiptAmount'])),
    methods: {
        commit(mutation, payload) {
            this.$store.commit(mutation, payload);
        },
        dispatch(action, payload) {
            this.$store.dispatch(action, payload);
        },
        proceed() {
            console.log('ready to proceed')
        }
    },
    components: {
        'vue-select': vueSelect,
    }
}
</script>

