<template>
  <div class="header__table-cell header__cta-form" id="asset-select">
    <div class="cta-form">
      <div class="cta-form__assets-selection">
        <div class="title">
          Choose which assets to trade
        </div>
        <div class="input-fields">
          <div class="select-component custom-form-group">
            <label for="deposit">Deposit</label>
            <vue-select :options="assets" v-model="depositAsset" label="name">
              <template slot="option" slot-scope="option">
                <!-- <span class="text"> -->
                <img :src="`http://localhost:9000/${option.imagePath}`" style="height:2rem; margin-right: .3rem; margin-bottom: 0; line-height: 1"> {{option.name}}
              </template>
            </vue-select>
          </div>
          <div class="select-component custom-form-group">
            <label for="receive">Receive</label>
            <vue-select :options="assets" v-model="receiptAsset" label="name">
              <template slot="option" slot-scope="option">
                <!-- <span class="text"> -->
                <img :src="`http://localhost:9000/${option.imagePath}`" style="height:2rem; margin-right: .3rem; margin-bottom: 0; line-height: 1"> {{option.name}}
              </template>
            </vue-select>
          </div>
          <div class="textbox-component">
            <label for="amount">
              Amount (Min: 0, Max: 100)
            </label>
            <input v-model="amount" type="number" id="amount" placeholder="0" />
          </div>
        </div>
        <div class="expected-amount">
          <div class="amount">
            {{depositAsset && receiptAsset? `${amount} ${depositAsset.code.toUpperCase()} (${receiptAmount}${receiptAsset.code.toUpperCase()})` : null}}
          </div>
          <div class="exchange-rate">
            {{depositAsset && receiptAsset? `1 ${depositAsset.code.toUpperCase()} = ${conversionRate.toFixed(8).replace(/(?:\.)?0+$/, "")}${receiptAsset.code.toUpperCase()}`: null}}
          </div>
        </div>
        <button class="convert btn-custom-astronaut-blue" @click="proceed">
          Convert
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import vueSelect from "vue-select";
export default {
  inject: ['global'],
  data() {
    return {
      depositAsset: null,
      receiptAsset: null,
      assets: [],
      amount: 0,
      request: null,
      conversionRate: null
    }
  },
  computed: {
    receiptAmount() {
      if (isNaN(this.amount) || isNaN(this.conversionRate)) return null;
      return (this.conversionRate * this.amount).toFixed(8).replace(/(?:\.)?0+$/, "")
    }

  },
  watch: {
    receiptAsset(newAsset, previousAsset) {
      if (!(this.depositAsset && this.receiptAsset)) return;
      this.UpdateRate(previousAsset ? {
        to: newAsset.code
      } : {
          from: this.depositAsset.code,
          to: newAsset.code
        });
    },
    depositAsset(newAsset, previousAsset) {
      if (!(this.depositAsset && this.receiptAsset)) return;
      this.UpdateRate(previousAsset ? {
        from: newAsset.code
      } : {
          from: newAsset.code,
          to: this.receiptAsset.code
        });
    }
  },
  methods: {
    UpdateRate(query) {
      console.log(query)
      if (!this.socket) {
        const socket = new socketIoClient("http://localhost:9000/rates", {
          reconnectionDelay: 10000,
          query
        })
        socket
          .on("seen", (msg) => console.log("seen: ", msg))
          .on("exception", err => console.log("exception: ", err))
          .on("new_rate", rate => {
            this.conversionRate = rate;
            console.log("new rate: ", rate);
          })
          ;
        this.socket = socket;
      }
      else this.socket.emit("parameter_change", query)
    },
    proceed() {
      if (!this.global.user) return this.$router.push({
        path: '/login',
        query: {
          message: 'Login to continue',
          nextPage: {
            path: '/transaction',
            query: {
              deposit: this.depositAsset.code,
              receipt: this.receiptAsset.code,
              amount: this.amount,
            },
          },
        },
      });
      this.$router.push({
        path: '/transaction',
        query: {
          deposit: this.depositAsset.code,
          receipt: this.receiptAsset.code,
          amount: this.amount,
        }
      });

    }
  },
  mounted() {
    this.$root.request("GET", `/assets/`, function(err, res) {
      if (err) console.error("couldn't load assets");
      this.assets = res;
    }.bind(this))
  },
  components: {
    "vue-select": vueSelect
  }
}
</script>